.header {
    margin: 2px;
    grid-area: h;
    background-color: cornflowerblue;
    border-radius: 6px 6px 6px 6px;
}

.header img {
    margin: 3px;
    width: 45px;
    height: 45px;
}

.loginBlock {
    float: right;
    color: white;
}

.loginBlock a {
    color: white;
}

.loginBlock button {
    border-radius: 6px;
}

.loginName {
    color: white;
    text-shadow: 0 1px 1px black;
}