.app-wrapper {
    margin: 0 auto;
    display: grid;
    /*width: 1200px;*/

    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 2fr;
    grid-template-columns: 2fr 8fr;
    grid-gap: 5px;
}

.app-wrapper-content {
    margin: 2px;
    grid-area: c;
    background-color: #779fec;
    border-radius: 6px 6px 6px 6px;
}