.nav {
    margin: 2px;
    padding: 20px;
    grid-area: n;
    background-color: cornflowerblue;
    border-radius: 6px 6px 6px 6px;
}

.item {
    margin-left: 10px;
    margin-top: 10px;
    color: white;
    text-shadow: 0 1px 1px black;
}

.item a {
    color: white;
    text-decoration: none;
}

.item a.activeLink {
    background-color: #92b6ed;
    border-radius: 4px;
}

